import { observer } from "mobx-react-lite";
import React, { SyntheticEvent }  from "react";
import { Reveal, Button } from "semantic-ui-react";
import { Profile } from "../../app/models/profile";
import { useStore } from "../../app/stores/store";

interface Props {
    profile : Profile;
}

export default observer(function FollowButton({profile}:Props) {
    const {profileStore, userStore} = useStore();
    const {upateFollowing , loading} = profileStore;

    if(userStore.user?.username === profile.username) return null;

    function handelFollow(e: SyntheticEvent, username: string) {
        e.preventDefault();
        profile.following ? upateFollowing(username, false) : upateFollowing(username , true);
     }
    return(
        <Reveal animated='move'>
        <Reveal.Content visible style={{width: '100%'}}>
            <Button 
                fluid 
                color='teal' 
                content={profile.following ? 'Following' : 'Not following'}
            />
        </Reveal.Content>
        <Reveal.Content hidden style={{width: '100%'}}>
            <Button 
                fluid 
                basic
                color={profile.following ? 'red' : 'green'} 
                content={profile.following ? 'Unfollow' : 'Follow'}
                onClick={(e) => handelFollow(e, profile.username)}
                loading={loading}
            />
        </Reveal.Content>
    </Reveal>
    )
})

 